.badges-showcase {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.badge {
    margin: 20px;
    width: 300px;
    border: 5px solid #365f86;
    border-radius: 10px;
}

.badge-header {
    padding: 10px;
    background-color: #91d3e0;
    color: #580606;
    font-family: "Courier New", Courier, monospace;
    font-weight: bold;
}

.badge-title {
    font-size: 28px;
    text-align: center;
}

.badge-title.master{
color: red;
}

.badge-subtitle {
    font-size: 18px;
    text-align: center;
}

.badge-body {
    padding: 10px;
    text-align: center;
    font-size: 22px;
}

.badge-footer {
    background-color: #19d9ea;
    height: 10px;
}
